@import "sass/index";

.wrapper {
  padding-top: 0;
}

.tableWrapper {
  padding-top: spacing(4);
}

.tableCellWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.tableWinnerGame {
  background: use(--subsidiary-blue);
}
.tableLoserGame {
  background: use(--subsidiary-red);
}
.tableCircle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 5px;
}
